

$skew: 16deg;
$overhang: 20%;
$colorPri: #6ABE26;

.links {
  width: 100%;
  height: 30em;
  background: $colorPri;
  &__left, &__right {
    position: relative;
    width: 50%;
    height: 100%;
    float: left;
    &__skew {
      position: absolute;
      width: 100% + $overhang;
      height: 100%;
      transform: skew(-$skew,0deg);
      left: -$overhang;
      overflow: hidden;
    }
  }
  &__right {
    &__skew {
      left: auto;
      right: -$overhang;
      background: darken($colorPri, 5%);
    } 
  }
}

.links__left__skew__img, .links__right__skew__img {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: skew($skew,0deg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  filter: grayscale(100%);
  opacity: 0.5;
}

.links__left__skew__img {
  right: -$overhang / 1.5;
  background-image: url(http://www.visualphotos.com/photo/1x7784608/side_view_of_a_horse_with_mouth_open_NP01132987.jpg);
}

.links__right__skew__img {
  left: -$overhang / 1.5;
  background-image: url(http://listverse.com/wp-content/uploads/2012/11/bad_smell.jpg);
}

@media (max-width: 600px) {
  .links {
    &__left, &__right {
      width: 100%;
      &__skew {
        width: 100%;
        transform: skew(0deg,0deg);
        left: auto;
        right: auto;
      }
    }
  }
  .links__left__skew__img, .links__right__skew__img {
    transform: skew(0deg,0deg);
    left: auto;
    right: auto;
  }
}